a.btn,
.btn,
.btn {
   height: 48px;
   border-radius: 24px;
   padding: 0 50px;
   font-size: 15px;
   line-height: 1;
   color: #FFFFFF;
   display: inline-flex;
   justify-content: center;
   align-items: center;
   transition: 0s;
   background-color: var(--mf-green);

   &:hover {
      background-color: #00EE6F;
   }

   &:focus {
      box-shadow: none;
   }
}

.btn.btn-fiolet {
   background-color: var(--mf-fiolet);

   &:hover {
      background-color: #B808D4;
   }
}

button.btn:disabled {
   background-color: #EDEDED;
   border: 0;
   box-shadow: inset 0 0 0 1px #d8d8d8;
   color: rgba(51,51,51,.5);
   opacity: 1;

   &:hover {
      cursor: auto;
   }
}

